/// Header Kasa ///

.kasa-container-header {
  max-width: 1240px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 45px;
  .kasa-header-section1 {
    display: flex;
    flex-direction: row;
    margin-bottom: 45px;
    justify-content: space-between;
    align-items: center;
    .kasa-ul-nav {
      display: flex;
      flex-direction: row;
      list-style-type: none;
      gap: 25px;
      .kasa-li-nav {
        color: #ff6060;
        font-weight: 500;
        font-size: 24px;
        &:hover {
          cursor: pointer;
          text-decoration: underline;
        }
      }
    }
  }
  .kasa-header-section2 {
      width: 1240px;
      margin-top: 45px;
    figure {
      img{
        z-index: 0;
        position: relative;
      }
      h1 {
        position: absolute;
        z-index: 2;
        left: 300px;
        bottom:50px;
        color: #fff;
        font-size: 48px;
        font-weight: 500;
      }
    }
  }
}


@media screen and (max-width: 375px) {
    .kasa-container-header {
      max-width: 375px;
      width: 90%;
      margin: auto;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      .kasa-header-section1 {
        display: flex;
        flex-direction: row;
        margin-bottom: 10px;
        justify-content: space-between;
        align-items: center;
      .kasa-logo-header{
        width: 145px;
        height: 45px;
      }
      .kasa-ul-nav {
        display: flex;
        flex-direction: row;
        .kasa-li-nav {
          font-size: 12px!important;
        }
      }
    }
  }
  .kasa-header-section2 {
    width: 335px;
    height: 111px;
    margin: auto;
    margin-bottom: 25px;
  figure {
    img{
      position: relative;
      width: 335px;
      height: 111px;
    }
    h1 {
      position: absolute;
      z-index: 2;
      top: 115px;
      left: 20px;
      color: #fff;
      font-size: 24px;
      font-weight: 500;
      }
    }
  }
}
