.accordeon-description{
    height: 52px;
    width: 582px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
    background-color: #FF6060;
    color: #fff;
    border-radius: 5px;
    font-size: 18px;
    cursor: pointer;
        h3{
            padding-left: 15px;
            font-weight: lighter;
        }
    
}

.reponse-description{
    width: 582px;
    color: #FF6060;
    background-color: #F6F6F6;
    padding: 0.5rem;
    font-size: 18px;
    text-align: left;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

@media  screen and (max-width: 375px) {
    .accordeon-description{
        width: 319px;
        height: 29px;
        font-size: 13px;
    }
    
    .reponse-description{
        width: 319px;
        font-size: 13px;
    }
}
