// style for slideshow at rental page //


.slideshow-container {
    position: relative;
    .img-slide-show{
        height: 415px;
        border-radius: 25px;
        width: 100%;
        object-fit: cover;
    }
    .container-arrows{
        width: 100%;
        height: 415px;
        display: flex;
        flex-direction: row;
        justify-content:space-between;
        position: absolute;
        align-items:center;
        .arrow{
        z-index: 2;
        display:flex;
        justify-content: space-between;
        cursor: pointer;
        transition: 1000ms;
        &:hover{
            transform: scale(1.15);
            transition: 1000ms;
        }
        }
        .arrow-left{
            margin-left: 15px;
        }
        .arrow-right{
            margin-right: 15px;
        }
    }
    
}


@media screen and (max-width: 375px) {
    .slideshow-container {
        position: relative;
        width: 335px;
        margin: auto;
        .img-slide-show{
            height: 255px;
            border-radius: 25px;
            width: 335px;
            object-fit: cover;
        }
        .container-arrows{
            width: 100%;
            height: 255px;
            display: flex;
            flex-direction: row;
            justify-content:space-between;
            position: absolute;
            align-items:center;
            .arrow{
            z-index: 2;
            display:flex;
            justify-content: space-between;
            width: 10%;
            cursor: pointer;
            transition: 1000ms;
            &:hover{
                transform: scale(1.15);
                transition: 1000ms;
            }
            }
            .arrow-left{
                margin-left: 15px;
            }
            .arrow-right{
                margin-right: 15px;
            }
        }
        
    }
}

